//@flow

export default function(chai: Object, utils: Object) {
  utils.addMethod(chai.Assertion.prototype, 'render', function(arg) {

    const wrapper = utils.flag(this, 'object');

    this.assert(
      wrapper.find(arg).exists() === true,
      `expected #{this} to contain #{arg}`,
      `expected #{this} not to contain #{arg}`,
    );
  });
}
